.footer{
    display: flex;
    align-items: center;
    margin: -0.5rem -3.5rem;
    position: relative;
    
}
.f-content{
    position: absolute;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin-top: 7rem;
    gap: 2rem;
    color: white;
}

.f-icons{
    display: flex;
    gap: 1rem;
    justify-content: center;

}

@media screen and (max-width: 480px){
    .f-content{
      transform: scale(.5);
    }
  }
  