.experience{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5rem;
    height: 30vh;
    margin-bottom: 8rem;
    
}
.achievement{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.circle{
    width: 3rem;
    height: 3rem;
    background: white;
    border-radius: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 2rem;
}
.circle::before{
   border-radius: 100%;
   content: '';
    background-image: linear-gradient(to bottom,#6f1616 0%,rgb(241, 214, 134));
    top: -7px;
    left: -7px;
    bottom: -7px;
    right: -7px;
    position: absolute;
    z-index: -1;
    box-shadow: var(--boxShadow);

}

 .achievement span:nth-of-type(2){
     color:var(--green);
 }


 @media screen and (max-width: 480px) {
    .experience{
        transform: scale(0.7);
    }
}













