.works{
    padding: 0 3rem 0 3rem;
    display: flex;
    height: 90vh;
    margin-top: 4rem;
}
.w-right{
    position: relative;

}
.w-mainCircle{
    left:9rem;
    position: relative;
    width: 17rem;
    height: 17rem;
    border-radius: 100%;
    box-shadow: var(--smboxShadow);
    top: 2rem;
    background: white;
}
.w-mainCircle>*{
    position: absolute;
}
.w-mainCircle>:nth-child(1){
    top: -3rem;
    left: 6rem;
}
.w-mainCircle>:nth-child(2){
    left: -3rem;
    top: 5rem;
}
.w-mainCircle>:nth-child(3){
    left: 6rem;
    top: 5rem;
}

.w-mainCircle>:nth-child(4){
    left: 15rem;
    top: 5rem;
}
.w-mainCircle>:nth-child(5){
    left: 6rem;
    top: 13rem;
}
.w-SecCircle{
width: 5rem;
height: 5rem;
position: absolute;
border-radius: 100%;
border: 5px solid #ebebeb;
display: flex;
align-items: center;
justify-content: center;
background:" none";
box-shadow: var(--smboxShadow);
background: white;
}
.w-SecCircle>img{
    transform:scale(0.5);
}

.w-backCircle{
  position: absolute;
  height: 13rem;
  width: 13rem;
  border-radius: 100%;
  z-index: -1;  
}
.blueCircle{
    background:rgb(12, 84, 112);
    left: 18rem;
    top: 0rem;
}
.yellowCircle{
    background: goldenrod;
    left: 18rem;
    top: 8rem;
}


@media screen and (max-width: 480px){
    .works{
      flex-direction: column;
      height: 55rem;
      padding: 0;
      gap: 5rem;
    }
    .w-right{
        display: flex;
        align-items: flex-start;
        justify-content: start;
        transform: scale(0.7);
    }
    .w-mainCircle{
        position: static;
    }
    .w-backCircle{
        left: 8rem !important;
    }
  }
  































