:root
{
  --green:rgb(5, 148, 5);
  --BrownCard:rgb(119, 9, 9);
  --yellow: #F5C32C;
    --orange : #FCA61F;
    --black : #242D49;
    --gray : #788097;
    --blueCard : #DDF8FE;
    --purple: rgb(238 210 255);
    --boxShadow : 0px 19px 60px rgb(0 0 0 / 8%);
    --orangeCard: rgba(252, 166, 31, 0.45);
    --smboxShadow: -79px 51px 60px rgba(0, 0, 0, 0.08);
}

.App{
    padding: 0.5rem 3.5rem;
    overflow: hidden;
    color: var(--black);    
}
.button{
  border-radius: 34px;
  border: none;
  color: white;
  font-size: 17px;
  padding: 2px 30px 2px 30px;
  background:linear-gradient(180deg,#70f989 26.71%,#047946 99.36%);
  box-shadow: 0px 20px 24px 3px rgba(5, 243, 89, 0.716);
}
 .button:hover{
   background: rgb(248, 249, 248);
   cursor: pointer;
   color: var(--green);
   border: 1px solid var(--green);
 }

  @media screen  and (max-width: 480px) {
    .App{
      padding:0.5rem 1rem;
    }
  }




















  