.portfolio{
    display:flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 70vh;
}
.portfolio span:nth-of-type(1){
    color: var(--black);
    font-size: 1.5rem;
    font-weight: bold;
}
.portfolio span:nth-of-type(2){
   color:var(--BrownCard) ; 
   font-size: 2rem;
   font-weight: bold;
}
/* slider*/
.portfolio .swiper{
    margin-top: 3rem;
    width: 100%;
}
.portfolio-slider .swiper-slide{
   width: 20rem; 
}
.portfolio img{
    width: 19rem;
    filter: drop-shadow(-12px 15px 13px rgba(0,0,0,0.25));
    border-radius: 19px;
}