.floatingdiv{
    justify-content: space-around;
    display: flex;
    background:rgb(255, 251, 251);
    box-shadow: var(--boxShadow);
    border-radius: 20px;
    align-items:center;
    padding: 0px 15px 0px 0px;
    height: 3.5rem;
    width: 9rem;
    
}
.floatingdiv>img{
    transform: scale(50%);
}