.toggle{
    display: flex;
    justify-content: space-between;
    border-radius: 1rem;
    border: 3px solid var(--orange);
    position: relative;
    padding: 2px;
    cursor: pointer;
}
.toggle>*{
    width: 1rem;
    height: 1rem;
    color: gold;

}
.t-button{
    border-radius: 100%;
    background:var(--orange);
    position: absolute;
}