.card{
    width:7rem;
    height: 12rem;
    position: absolute;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
    text-align: center;
    background:rgba(190, 186, 186, 0.26);
    border:7px solid var(--BrownCard);
    box-shadow: var(--boxShadow);
    border-radius:20px;
    padding: 0px 26px 2rem 26px;
}
.card span:nth-of-type(2){
    color: var(--gray);
    font-size: 16px; 
}

.card>img{
    transform: scale(0.5);
    margin-bottom: -2rem;

}

.c-button{
background:rgb(248, 247, 247);
box-shadow: 0px 19px 60px rgba(0, 0, 0, 0.08);
border-radius: 7px;
border: none;
padding: 10px;
font-size: 16px;
color: rgb(193, 159, 12);
}

@media screen  and (max-width: 480px){
    .card{
        width: auto;
    }
}













